body {
  min-width: 320px;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color: #191919;
}
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  font-size: 12px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  text-transform: uppercase;
}
input,
button,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
img {
  max-width: 100%;
}
h1,
h2,
h3 {
  font-weight: 400;
}
.line-break {
  display: block;
}
@media (min-width: 768px) {
  .line-break {
    display: inline-block;
  }
}
header {
  margin-bottom: 65px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.header-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 35px 0;
  justify-content: space-around;
  border-bottom: 1px solid #dcdcdc;
  width: 250px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header-inner {
    justify-content: center;
    width: 580px;
    justify-content: space-between;
  }
}
@media (min-width: 1260px) {
  .header-inner {
    padding: 40px 0;
    width: 1016px;
  }
}
.gamburger {
  position: relative;
  bottom: 5px;
  left: 0;
  display: block;
  cursor: pointer;
}
.gamburger span {
  position: relative;
  left: -10px;
  width: 20px;
  height: 4px;
  background-color: #767676;
  display: inline-block;
  cursor: pointer;
}
@media (min-width: 768px) {
  .gamburger span {
    display: none;
  }
}
.gamburger span:before {
  content: "";
  cursor: pointer;
  position: absolute;
  top: 6.5px;
  right: 0px;
  width: 20px;
  height: 4px;
  background-color: #767676;
}
.gamburger span:after {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 13px;
  right: 0px;
  width: 20px;
  height: 4px;
  background-color: #767676;
}
.main-menu {
  display: none;
  list-style-type: none;
  padding: 0;
  order: 1;
  position: relative;
  left: -40px;
}
@media (min-width: 768px) {
  .main-menu {
    width: 84%;
    padding-right: 40px;
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1260px) {
  .main-menu {
    justify-content: space-between;
    align-items: center;
    width: 93%;
  }
}
.main-menu__item {
  cursor: pointer;
  padding: 15px 0 15px 0;
}
.main-menu__item:last-child {
  padding-bottom: 0;
}
@media (min-width: 768px) {
  .main-menu__item {
    padding-right: 70px;
  }
  .main-menu__item:last-child {
    margin-left: 57px;
  }
}
@media (min-width: 1260px) {
  .main-menu__item {
    padding: 0;
  }
  .main-menu__item:last-child {
    margin: 0;
  }
}
.main-menu__item a {
  text-decoration: none;
  color: #000;
}
.switcher {
  color: #888888;
  display: flex;
}
@media (min-width: 768px) {
  .switcher {
    display: flex;
    align-items: flex-end;
    padding-bottom: 30px;
  }
}
@media (min-width: 1260px) {
  .switcher {
    padding: 0;
    align-items: center;
  }
}
.switcher__btn {
  padding-left: 10px;
  display: inline-block;
  cursor: pointer;
  align-self: center;
}
.switcher__btn-active {
  color: #000;
}
.logo,
.logo-item,
.footer-logo {
  font-family: 'Comfortaa', cursive;
  font-size: 18px;
  font-weight: 200;
  text-transform: lowercase;
}
@media (min-width: 768px) {
  .logo-item {
    display: none;
  }
}
@media (min-width: 1260px) {
  .logo-item {
    display: block;
    font-size: 22px;
    padding-bottom: 10px;
  }
}
.logo {
  display: block;
}
@media (min-width: 768px) {
  .logo {
    margin-bottom: 15px;
    text-align: center;
    width: 100%;
    font-size: 22px;
  }
}
@media (min-width: 1260px) {
  .logo {
    display: none;
  }
}
.offers img {
  max-width: 226px;
  max-height: 190px;
}
@media (min-width: 768px) {
  .offers img {
    max-width: 452px;
    max-height: 252px;
  }
}
.offers__title {
  font-size: 18px;
}
@media (min-width: 1260px) {
  .offers__title {
    order: 1;
  }
}
@media (min-width: 1260px) {
  .offers a {
    order: 1;
  }
}
.offers__size {
  display: none;
}
@media (min-width: 768px) {
  .offers__size {
    display: inline-block;
    text-align: right;
    font-size: 50px;
    position: absolute;
    top: 30px;
    right: 50px;
  }
}
@media (min-width: 768px) and (min-width: 1260px) {
  .offers__size {
    order: 1;
  }
}
.offers__view {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1260px) {
  .offers__view {
    flex-direction: row;
    flex-wrap: wrap;
    width: 1016px;
    margin: 0 auto;
    justify-content: space-between;
  }
}
.offers__view-bag {
  width: 248px;
  border: 1px solid #dcdcdc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  padding: 35px 14px 35px 20px;
  height: 376px;
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
@media (min-width: 768px) {
  .offers__view-bag {
    width: 580px;
    height: 423px;
    padding: 45px 40px 40px 35px;
  }
}
@media (min-width: 1260px) {
  .offers__view-bag {
    width: 488px;
    height: 516px;
    margin-bottom: 45px;
  }
}
footer {
  background-color: #2b2b32;
  text-align: center;
  padding: 36px 0 40px;
  color: #fff;
}
.phone,
.email {
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .phone,
  .email {
    width: 40%;
  }
}
.phone span:last-child,
.email span:last-child {
  color: #888888;
  font-size: 14px;
  margin: 20px 0 25px;
}
@media (min-width: 768px) {
  .phone span:last-child,
  .email span:last-child {
    font-size: 16px;
  }
}
.footer-logo {
  font-size: 22px;
  margin: 30px 0 20px 0;
}
@media (min-width: 768px) {
  .footer-logo {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .footer__inner {
    width: 580px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (min-width: 1260px) {
  .footer__inner {
    width: 1016px;
  }
}
.footer__inner .footer-logo:before {
  content: '';
  position: relative;
  display: block;
  height: 1px;
  width: 209px;
  background: #888;
  right: 0;
  left: 0;
  bottom: 25px;
  margin: auto;
}
@media (min-width: 768px) {
  .footer__inner .footer-logo:before {
    width: 100%;
  }
}
.footer__inner .rights {
  font-size: 14px;
  color: #888888;
  text-transform: none;
}
@media (min-width: 768px) {
  .footer__inner .rights {
    width: 100%;
    font-size: 16px;
  }
}
.offers__view {
  display: flex;
  flex-wrap: wrap;
}
.popup-container {
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  transform: scale(1.3);
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(21, 17, 17, 0.61);
  display: flex;
  align-items: center;
}
.popup-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 50%;
}
.popup-content a.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  background: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
}
.popup-content a.close:hover {
  color: #333;
}
.popup-container:target {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.popup-container h3 {
  margin: 10px;
}
@media (min-width: 1260px) {
  .fruits .offers__view-bag {
    width: 320px;
    height: 362px;
  }
}
@media (min-width: 768px) {
  .fruits img {
    width: 100%;
    height: 100%;
  }
}
.about {
  width: 250px;
  margin: 0 auto;
}
.about a {
  color: #0079c1;
}
.about p {
  padding: 0;
  margin: 0;
  text-transform: none;
  font-size: 16px;
  text-align: justify;
  color: #666;
}
.about__advantages {
  margin: 0 0 40px 0;
  padding: 0;
  text-transform: none;
  font-size: 16px;
  text-align: justify;
  color: #666;
}
@media (min-width: 768px) {
  .about__advantages {
    margin-bottom: 60px;
  }
}
@media (min-width: 1260px) {
  .about__advantages {
    margin-bottom: 75px;
  }
}
.about li {
  list-style-type: none;
}
@media (min-width: 768px) {
  .about {
    width: 580px;
  }
}
@media (min-width: 1260px) {
  .about {
    width: 1016px;
  }
}
.about__source {
  margin-bottom: 35px;
}
.about__title {
  font-size: 18px;
}
.about__advantages {
  margin-bottom: 40px;
}
.contact {
  width: 248px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contact {
    width: 580px;
  }
}
@media (min-width: 1260px) {
  .contact {
    width: 1016px;
  }
}
.contact__map {
  width: 100%;
  height: 220px;
}
@media (min-width: 768px) {
  .contact__map {
    height: 365px;
  }
}
@media (min-width: 1260px) {
  .contact__map {
    width: 500px;
    height: 313px;
    padding-top: 5px;
  }
}
@media (min-width: 1260px) {
  .contact__adress {
    width: 488px;
    margin-left: 30px;
  }
}
.contact__office,
.contact__stock {
  margin-bottom: 40px;
}
@media (min-width: 1260px) {
  .contact__office,
  .contact__stock {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.contact__stock-title,
.contact__office-title {
  font-size: 14px;
}
@media (min-width: 768px) {
  .contact__stock-title,
  .contact__office-title {
    font-size: 18px;
  }
}
@media (min-width: 1260px) {
  .contact__stock-title,
  .contact__office-title {
    margin: 0;
  }
}
.contact__title {
  font-size: 18px;
}
.contact p {
  color: #666;
  font-size: 14px;
  text-transform: none;
}
@media (min-width: 768px) {
  .contact p {
    font-size: 16px;
  }
}
